import Vue from "vue";
import VueRouter from "vue-router";
import Dashboard from "../containers/Dashboard/index.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "products",
    component: Dashboard
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
